.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #32325d;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
* {
  padding-left: 0;
}
#container {
  width: 400px;
  height: 500px;
  padding-bottom: 10px;
  overflow-x: hidden;
  display: block;
  box-sizing: border-box;
  margin: 0;
  background-color: #fff;
  overflow: hidden;
  border: 1px solid #a1a1a1;
  border-radius: 1px;
}
#innerContainer {
  width: 100%;
  height: 283px;
  margin: 0;
  /* background-color: #323f68; */
}
