.inpt {
  display: flex;
  height: 30px;
  margin-left: 20px;
  margin-right: 20px;
  padding-top: 10px;
  padding-bottom: 10px;
}
.text {
  background-color: #1b8afd;
  border-radius: 5px;
  border: 0;
  width: 280px;
  height: 26px;
  color: #ffffff;
  font-weight: bold;
  text-overflow: ellipsis;
  overflow: hidden;
  outline: none;
  padding-left: 3px;
  padding-right: 3px;
  box-shadow: 0px 2px 7px rgba(0, 0, 0, 0.6);
}
.btn {
  width: 25px;
  height: 25px;
  padding-left: 10px;
  padding-bottom: 2px;
  background: none;
  border: none;
  font-size: x-large;

  color: #1b8afd;
}
.solid {
  margin: 0;
  padding: 0;
  width: 100%;
  position: relative;
  border-top: 1px solid #1b8afd;
  border-bottom: 1px solid #1b8afd;
  margin-bottom: 5px;
}
.selector {
  margin-left: 10px;
  font-weight: bold;
  width: 140px;
  border-radius: 5px;
  background-color: #1b8afd;
  border: none;
  height: 28px;
  color: #ffffff;
  box-shadow: 0px 2px 7px rgba(0, 0, 0, 0.6);
}

.logo {
  padding-right: 20px;
  height: 35px;
}
