.nav ul {
  list-style: none;
  background-color: #f6f9fc;
  text-align: center;
  width: 100%;
  margin: 10px 0 0 0;
}
.nav li {
  font-family: "Oswald", sans-serif;
  width: 125px;
  border-bottom: none;
  height: 29px;
  line-height: 29px;
  font-size: 1.2em;
  display: inline-block;
}

.nav a {
  text-decoration: none;
  color: #ababab;
  display: block;
  transition: 0.3s background-color;
  height: 29px;
  border-radius: 5px;
}

.nav a:hover {
  background-color: #97c7fb;
  height: 29px;
}

.nav a.clicked {
  /* background-color: #fff; */
  color: #1b8afd;
  cursor: default;
}

.nav .nav-title {
  padding-left: 5px;
  font-size: medium;
  vertical-align: super;
}

.nav svg {
  font-size: x-large;
}
