.table {
  width: calc(100% - 7px);
  padding-top: 2px;
  padding-bottom: 2px;
  margin-bottom: 10px;
  font-size: medium;
  border: 0px solid #cecece;
  border-radius: 5px;
  box-shadow: 1px 2px 3px #97c7fb67;
  background-color: #fff;
  margin-right: 3px;
}
.details {
  display: flex;
  width: calc(100%);
  margin-bottom: 2px;
  margin-right: 3px;
}

.divide {
  border: none;
  height: 1px;
  /* border-top: 1px solid #8bb9b8;
    border-bottom: 1px solid #8bb9b8; */
  background-color: #97c7fb67;
}

p {
  text-overflow: ellipsis;
  overflow: hidden;
  width: calc(100% - 20px);
  overflow-y: auto;
  white-space: pre-line;
}
.link {
  text-overflow: ellipsis;
  overflow: hidden;
  width: calc(100% - 20px);
  overflow-y: auto;
  white-space: pre-line;
  cursor: pointer;
}
.link:hover {
  color: #97c7fb;
}
.right {
  text-align: end;
  display: inline-block;
  overflow: hidden;
  margin-right: 10px;
  font-size: 13px;
  font-weight: bold;
  color: #7a89b2;
}
.left {
  text-align: start;
  display: inline-block;
  overflow: hidden;
  margin-left: 10px;
  font-size: 13px;
  /* font-weight: bold; */
  color: #7a89b2;
}
