.wallet {
  /* margin-top: 10px; */
  height: 99%;
  max-height: 280px;
  overflow-y: auto;
  overflow-x: hidden;
  /* margin-bottom: 10px; */
}
.loader {
  top: 100px;
  display: block;
  margin: auto;
  padding: 3px;
  margin-top: 100px;
  width: 50px;
  animation: rotation 2s infinite linear;
  text-align: center;
}
.loadertext {
  text-align: center;
  vertical-align: middle;
  width: 100%;
  color: #1b8afd;
  font-weight: 600;
}

@keyframes rotation {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(359deg);
  }
}
::-webkit-scrollbar {
  width: 3px;
}
/* Track */
::-webkit-scrollbar-track {
  background: #cecece;
}
/* Handle */
::-webkit-scrollbar-thumb {
  background: #97c7fb;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}
