.nftDesc,
.nftTitle {
  width: 160px;
  margin-bottom: 5px;
}
.nftImage {
  height: 120px;
  object-fit: contain;
  margin: auto;
  width: 100%;
}
.nftTitle {
  /* height: 35px; */
  max-height: 42px;
  margin-left: 5px;
  margin-right: 5px;
  margin-top: 5px;
  color: #7a89b2;
  font-size: large;
  font-weight: 600;
}
.nftDesc {
  height: 90px;
  margin-left: 5px;
  margin-right: 5px;
  color: #7a89b2;
  font-size: small;
}

.ntfTable {
  border-radius: 5px;
  box-shadow: 1px 2px 3px #97c7fb67;
  margin-top: 8px;
  width: 95%;
  /* width: 46%; */
  margin-left: 6px;
  padding: 2px;
  display: flex;
}
